<template>
  <div class="find-rank">
    <div class="rank-tab">
      <div class="rank-tab-item-warp">
        <div
          v-for="item in tabList"
          :key="item.type"
          class="rank-tab-item"
          :class="{ active: active === item.type }"
          @click="videoRank(item.type)">
          {{ item.lable }}
        </div>
      </div>
    </div>
    <refresh-list
      class="find-rank-content"
      :refresh-loading.sync="refreshLoading"
      :list-loading.sync="listLoading"
      :finished="finished"
      :error.sync="error"
      @refresh="onRefresh"
      @load="onLoad">
      <div
        v-for="(video, index) in videoList"
        :key="video.id.toString()"
        class="video-item"
        @click="goPlayVideo(video.id)">
        <a
          v-show="isShowAd"
          v-if="index % 5 === 0 && ad.length > 0"
          :href="ad[index % (ad.length - 1)].url"
          target="_blank">
          <base-image
            style="margin-bottom: 15px;"
            class="video-item-img"
            height="40vw"
            :url="ad[index % (ad.length - 1)].imgbase64.url"
            :type="ad[index % (ad.length - 1)].imgbase64.type"
            encrypt/>
        </a>

        <div class="video-img-box" >
          <base-image
            class="video-item-img"
            height="50vw"
            :url="video.coverbase64.url"
            :type="video.coverbase64.type"
            encrypt/>
          <div class="video-info">
            <div class="video-info-item">
              <img src="../../assets/images/mine/icon_day.png" />{{
                timeRender(video.created_at)
              }}
            </div>
            <div class="video-info-item">
              <img src="../../assets/images/mine/icon_fire.png" />
              {{ (video.pageviews / 10000).toFixed(0) + "万" }}
            </div>
            <div class="video-info-item">
              <img src="../../assets/images/mine/icon_time.png" />{{
                video.playtimes
              }}
            </div>
          </div>
        </div>
        <div class="ideo-title">{{ video.title }}</div>
      </div>
    </refresh-list>
  </div>
</template>

<script>
import { timeRender, shuffle } from '@/utils/index'
import { mapState } from 'vuex'
import baseImage from '@/components/baseImage'
import refreshList from '@/components/refreshList'
import { rankList, adList } from '@/API/find'
export default {
  components: {
    baseImage,
    refreshList
  },
  data () {
    return {

      refreshLoading: false,
      finished: false,
      listLoading: false,
      error: false,
      tabList: [
        { lable: '播放数', type: 'pageviews' },
        { lable: '最新', type: 'new' },
        { lable: '点赞数', type: 'zan' }
      ],
      active: 'new',
      videoList: [],
      page: 1,
      ad: []
    }
  },
  computed: {
    ...mapState([
      'isShowAd'
    ])
  },
  mounted () {
    this.getAd()
  },
  methods: {
    timeRender (...arg) {
      return timeRender(...arg)
    },
    async onRefresh () {
      this.videoList = []
      this.page = 1
      await this.adHandler()
      await this.videoRank()
      this.$toast('刷新成功')
      this.refreshLoading = false
    },
    goPlayVideo (id) {
      this.$router.push({
        path: '/playVideo',
        query: {
          id
        }
      })
    },
    onLoad () {
      this.videoRank()
    },
    async videoRank (type) {
      if (type) {
        if (type !== this.active) {
          this.page = 1
          this.videoList = []
          await this.adHandler()
        }
        this.active = type
      } else {
        type = this.active
      }

      try {
        const { rescont } = await rankList(type, this.page)
        if (!(rescont.data instanceof Array)) {
          // 兼容
          rescont.data = Object.values(rescont.data).filter(
            e => e.pageviews !== undefined
          )
        }
        this.videoList = this.page === 1 ? rescont.data : this.videoList.concat(rescont.data)
        this.page = this.page + 1
        if (rescont.next_page_url === null) {
          this.listLoading = false
          this.finished = true
          return
        }
      } catch (error) {
        console.warn(error)
        this.error = true
      }
      this.finished = false
      this.listLoading = false
    },
    async getAd () {
      await adList().then(data => {
        if (data.rescont) {
          this.ad = shuffle(data.rescont)
        }
      })
    },
    async adHandler () {
      if (this.ad.length === 0) {
        await this.getAd()
      } else {
        this.ad = shuffle(this.ad)
      }
    }
  }
}
</script>

<style lang="css">
.find-rank {
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow: hidden;
}

.rank-tab {
  font-size: 14px;
  color: rgba(188, 189, 193, 1);
}

.rank-tab-item-warp {
  display: flex;
  justify-content: center;
}

.rank-tab-item {
  padding: 3px 10px;
  margin: 5px;
  background-color: #242630;
  border-radius: 10px;
}

.rank-tab-item.active {
  color: #ff5c38;
  border: solid 1px #ff5c38;
}

.find-rank-content {
  flex: 1;
  overflow: auto !important;
}

.video-item {
  margin-bottom: 15px;
}

.video-item-img {
  width: 100%;
  min-height: 163px;
}

.ideo-title {
  padding: 5px;
  font-size: 14px;
  color: #fff;
  text-align: left;
}

.video-img-box {
  position: relative;
}

.video-info {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.6);
}

.video-info div {
  color: #fff;
}

.video-info div:nth-child(2) {
  flex: 1;
}

.video-info-item {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  font-size: 16px;
}

.video-info-item img {
  width: 16px;
  margin-right: 8px;
  margin-left: 8px;
}
</style>



